* { padding:0; margin:0; box-sizing:border-box; -ms-user-select: none; -moz-user-select: -moz-none; 
    -khtml-user-select: none; -webkit-user-select: none; user-select: none; }
section, header, footer, aside, article, nav { display:block; }
img { border:0; vertical-align:top; -webkit-user-drag: none; }
li { list-style:none; list-style-position:inside; }
a { text-decoration:none; color: #FFF;}
h1, h2, h3, h4, h5, h6 { font-weight:normal; }
address { font-style:normal; }
table, td, th { border-collapse:collapse; }
button { background-color: transparent; border: transparent; cursor: pointer; }